import {Link} from 'react-router-dom'
import Icon from '../../components/Icon/Icon'

import './FormPage.sass'
import {useForm} from 'react-hook-form'
import FormInput from '../../components/FormInput'

export default function FormPage()
{

  // Handle form
  interface Form
  {
    firstName: string,
    lastName: string,
    password: string
  }

  const form = useForm<Form>()

  function onFormSubmit(formData: Form)
  {
    // Fetch data
    const {firstName, lastName, password} = formData

    // Print data
    alert(`
      : ${firstName}
      : ${lastName}
      : ${password}
    `)

    // Reset form
    form.reset()
  }

  // Render
  return (
    <div className="FormPage">

      <div className="container">

        <h1>  </h1>

        <p>
                useForm.     ,   
           
        </p>

        <form onSubmit={form.handleSubmit(onFormSubmit)}>

          <FormInput
            label=" " name="firstName" registerFunc={form.register} isErrored={!!form.formState.errors.firstName}
          />
          <FormInput
            label=" " name="lastName" registerFunc={form.register}
            isErrored={!!form.formState.errors.lastName}
          />
          <FormInput
            label="" name="password" registerFunc={form.register} isErrored={!!form.formState.errors.password}
          />

          <div className="form-separator __submit">
            <button className="_wa">
              <span></span>
            </button>
          </div>

        </form>
      </div>

      <div className="container">
        <Link to="/" className="_noUnderline">
          <button className="_zeroed">
            <Icon icon="arrow-64" />
            <span></span>
          </button>
        </Link>
      </div>
    </div>
  )
}
